import { Component, OnInit } from "@angular/core";
import { GlobalDataService } from "src/app/services/global-data.service";
import { map, filter, startWith, debounceTime } from "rxjs/operators";
import { countriesReversed } from "src/app/data/countries";
import { FormControl } from "@angular/forms";

const iconBarSvg = {
  width: 1000,
  height: 1000,
  name: "newplotlylogo",
  svg:
    '<svg enable-background="new 0 0 50 50" height="50px" id="Layer_1" version="1.1" viewBox="0 0 50 50" width="50px" xml:space="preserve" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><rect fill="none" height="31" stroke="#000000" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2" width="12" x="3" y="18"/><rect fill="none" height="20" stroke="#000000" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2" width="12" x="19" y="29"/><rect fill="none" height="44" stroke="#000000" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2" width="12" x="35" y="5"/><rect fill="none" height="50" width="50"/></svg>',
};

const lineChartSvg = {
  width: 1000,
  height: 1000,
  name: "newplotlylogo",
  svg:
    '<svg enable-background="new 0 0 50 50" height="50px" id="Layer_1" version="1.1" viewBox="0 0 50 50" width="50px" xml:space="preserve" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><rect fill="none" height="50" width="50"/><circle cx="2" cy="25" r="2"/><circle cx="15" cy="19" r="2"/><circle cx="25" cy="11" r="2"/><circle cx="35" cy="17" r="2"/><circle cx="48" cy="5" r="2"/><circle cx="2" cy="39" r="2"/><circle cx="15" cy="41" r="2"/><circle cx="25" cy="33" r="2"/><circle cx="35" cy="43" r="2"/><circle cx="48" cy="31" r="2"/><polyline fill="none" points="2,25 15,19 25,11   35,17 48,5 " stroke="#000000" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2"/><polyline fill="none" points="2,39 15,41 25,33   35,43 48,31 " stroke="#000000" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2"/></svg>',
};

@Component({
  selector: "app-page-global",
  templateUrl: "./page-global.component.html",
  styleUrls: ["./page-global.component.scss"],
})
export class PageGlobalComponent implements OnInit {
  METRICS = [
    { v: "confirmed", n: "Confirmed" },
    { v: "deaths", n: "Deaths" },
    // { v: "recovered", n: "Recovered" },
  ];

  typesCalc = [
    { v: "cummulative", n: "Cummulative" },
    { v: "daily", n: "Daily" },
    // { v: "recovered", n: "Recovered" },
  ];

  // selectedMetric: string
  metricCtrl = new FormControl("confirmed");
  typeCtrl = new FormControl("cummulative");
  normalizeInitialCtrl = new FormControl(0);
  countryCodes: string[];
  isLine = true;

  constructor(public globalSvr: GlobalDataService) {
    this.metricCtrl.valueChanges
      .pipe(filter((v) => !!v))
      .subscribe((metric) => {
        console.log("metric changed");
        this.globalSvr.triggerCountriesDataSubject();
      });

    this.typeCtrl.valueChanges.pipe(filter((v) => !!v)).subscribe((type) => {
      console.log("type changed");
      if (type != 'cummulative')
        this.isLine = false
      this.handleLineOrBar();
    });

    this.normalizeInitialCtrl.valueChanges.pipe(
      debounceTime(250)
    ).subscribe((type) => {
      console.log("normalizeInitialCtrl changed");
      this.globalSvr.triggerCountriesDataSubject();
    });
  }

  graph$ = this.globalSvr.countriesData$.pipe(
    filter((values) => !!values.length),
    map((values) => {
      console.log('IN GRAPH PIPE');
      const timelinesContries = values.map(
        (value) => value.location.timelines[this.metricCtrl.value].timeline
      );
      const timelinesArrays: any[] = timelinesContries.map((timeline) => {
        const x = Object.keys(timeline);
        const y = x.map((x_iter) => timeline[x_iter]);
        return { x, y };
      });
      const normStart = this.normalizeInitialCtrl.value
      let normalizedStartCountries = timelinesArrays
      if (normStart > 0) {
        normalizedStartCountries = timelinesArrays.map(caseCountry => {
        let filteredCumm = 0
        try {
          filteredCumm = caseCountry.y.findIndex(y => y > normStart)
        } catch (error) {
          console.log('Error in normalization starting value');
        }
          return {
            x: caseCountry.x.slice(filteredCumm).map((v,i) => i),
            y: caseCountry.y.slice(filteredCumm)
          }
        })
      }
      const timeLinesArraysAdjusted = normalizedStartCountries.map((arr) =>
        this.adjusttimeSeriesToCalcType(arr)
      );
      const traces = timeLinesArraysAdjusted.map((countryMod, i) => {
        return {
          x: countryMod.x,
          y: countryMod.y,
          type: "scatter",
          mode: "lines+markers",
          // marker: { color: "red" },
          name: values[i].location.country,
        };
      });
      const obj = {
        layout: {
          // width: '100%',
          // height: 640,
          title: `Metric: ${this.metricCtrl.value} cases`,
        },
        data: traces,
      };
      return obj;
    })
  );

  adjusttimeSeriesToCalcType(array: { x: string[] | number[]; y: number[] }) {
    const typeCalc = this.typeCtrl.value;
    if (typeCalc == "cummulative") return array;
    else if (typeCalc == "daily") {
      const prev = array.y.slice(0, array.y.length - 1);
      const diff = array.y.slice(1).map((v, i) => v - prev[i]);
      return { ...array, y: diff };
    }
  }

  ngOnInit(): void {
    // const codes = ["US", "BR"];
    // var requestOptions = <RequestInit>{
    //   method: 'GET',
    //   redirect: 'follow'
    // };
    // fetch("https://thevirustracker.com/free-api?countryTimeline=US", requestOptions)
    //   .then(response => response.text())
    //   .then(result => console.log(result))
    //   .catch(error => console.log('error', error));
    // this.globalSvr.addCountryData(codes);
  }

  handleCountrySelection(countryNames: string[]) {
    this.countryCodes = countryNames.map((v) => countriesReversed[v]);
    this.getData();
  }

  getData() {
    // return
    this.globalSvr.addCountryData({
      countryCodes: this.countryCodes,
      metric: this.metricCtrl.value,
    });
  }


  handleLineOrBar() {
    const typeplt = this.isLine ? "scatter" : "bar";
    const nameplt = !this.isLine ? "Lines" : "Bars";
    const iconplt = !this.isLine ? lineChartSvg : iconBarSvg;
    this.graph$ = this.graph$.pipe(
      map((graph) => {
        graph.data.forEach((trace) => {
          trace.type = typeplt;
        });
        return graph;
      })
    );
    const newModeBar = [
      [
        {
          name: `Show as ${nameplt}`,
          icon: iconplt,
          click: (element) => {
            this.handleLineOrBar();
          },
        },
      ],
      ["toImage"],
      ["pan2d"],
      ["select2d"],
      ["zoomIn2d"],
      ["zoomOut2d"],
      ["autoScale2d"],
      ["resetScale2d"],
    ];
    this.plotlyConfigRef.modeBarButtons = newModeBar;
    this.isLine = !this.isLine;
  }

  modeBarButtons = [
    [
      {
        name: "Show as Bars",
        icon: iconBarSvg,
        click: (element) => {
          this.handleLineOrBar();
        },
      },
    ],
    ["toImage"],
    ["pan2d"],
    ["select2d"],
    ["zoomIn2d"],
    ["zoomOut2d"],
    ["autoScale2d"],
    ["resetScale2d"],
  ];

  plotlyConfigRef = {
    displaylogo: false,
    scrollZoom: false,
    //https://github.com/plotly/plotly.js/blob/master/src/components/modebar/buttons.js
    modeBarButtons: this.modeBarButtons,
  };
}
