import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageGlobalComponent } from './pages/page-global/page-global.component';
import { PageBrazilComponent } from './page-brazil/page-brazil.component';


const routes: Routes = [
  {path: '', component: PageGlobalComponent},
  {path: 'brazil', component: PageBrazilComponent},
  {path: 'analyzes', component: PageBrazilComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
